@import url(https://fonts.googleapis.com/css2?family=Fahkwang:wght@600&family=Muli:ital,wght@0,400;0,900;1,700&display=swap);
body {
  margin: 0;
  font-family: 'Muli', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1, h2, h3 {
  font-family: 'Fahkwang';
}

p, li, a {
  color: black;
  text-decoration: none;
}

header{margin-top:20px;width:100%}header h1{font-family:Fahkwang;font-style:normal;font-weight:600;font-size:18px;line-height:23px;float:left;color:black}header nav{float:right}header nav ul{list-style:none;display:none;flex-direction:row}@media (min-width: 768px){header nav ul{display:flex}}header nav ul li{padding-left:5vw}header nav ul li a{font-family:Muli;font-style:normal;font-weight:normal;font-size:14px;line-height:18px}

.c-booking-panel{position:fixed;z-index:999;top:0;right:0;transform:translate(0%, 0%);width:100%;opacity:1;background-color:white;height:100%}@media (min-width: 768px){.c-booking-panel{height:100vw}}.c-booking-panel__container{max-width:1280px;margin-left:auto;margin-right:auto;display:flex;flex-wrap:wrap;flex-direction:row;margin-top:40px}.c-booking-panel__image{width:100%}@media (min-width: 768px){.c-booking-panel__image{width:75%}}.c-booking-panel__image img{width:100%}.c-booking-panel__form{width:100%}@media (min-width: 768px){.c-booking-panel__form{width:25%}}.c-booking-panel__form--inner{padding-left:20px}.c-booking-panel__form--inner h2{margin-top:0}.c-booking-panel__copy{width:100%}@media (min-width: 768px){.c-booking-panel__copy{width:70%}}.c-booking-panel__close{position:fixed;z-index:998;top:0px;left:0px;right:0px;bottom:0px;width:100%;height:100vh;opacity:0.2;background-color:black}

.f-forms .f-forms__element{display:block;width:100%;padding:10px 0;position:relative}.f-forms .f-forms__element label{display:block;position:relative;left:0}.f-forms .f-forms__validation--notice{display:none}

.f-forms .f-forms--number-selector-wrapper{display:flex;align-items:center}.f-forms .f-forms--number-selector-wrapper i{display:inline-flex;height:30px;width:30px;min-width:30px;background:black;position:relative;cursor:pointer;font-size:22px;justify-content:center;margin:10px 0;color:white;font-style:inherit}.f-forms .f-forms--number-selector-wrapper .f-forms--number-selector__input{width:60px;padding:6px 20px 6px 20px;text-align:center;border-radius:0;border:1px solid black}

.pagewrap{width:100%}main{position:relative}section,header{position:relative;width:100%;max-width:1280px;margin-left:auto;margin-right:auto}section.full-width,header.full-width{max-width:1440px}section::after,header::after{clear:both;content:"";display:block}.row-split{display:flex;flex-direction:column}@media (min-width: 768px){.row-split{flex-direction:row}}.row-split>div:nth-child(1){width:100%}@media (min-width: 768px){.row-split>div:nth-child(1){width:25%;margin-right:5%}}.row-split>div:nth-child(2){width:100%}@media (min-width: 768px){.row-split>div:nth-child(2){width:70%}}.row-split>div:only-child{width:100%}@media (min-width: 768px){.row-split>div:only-child{width:100%}}.row-split img{max-height:300px;overflow:hidden}.c-hero{display:flex;align-items:top;width:100%;margin:40px 0;background-size:100%;background-repeat:no-repeat;min-height:65vh}.c-hero__content{margin-top:15vw;margin-left:7vw;width:50%}@media (min-width: 768px){.c-hero__content{width:100%}}.c-hero__content h2{font-family:Fahkwang;font-style:normal;font-weight:600;line-height:23px;letter-spacing:1.63636px;color:white;font-size:18px}.c-hero__content p{font-family:Muli;font-style:normal;font-weight:normal;font-size:14px;line-height:18px;letter-spacing:1.27273px;color:white}.c-hero__content .button-primary{background-color:white;padding:10px 20px;display:inline-block;opacity:1;transition:all 0.3ms ease-in-out}.c-hero__content .button-primary:hover{opacity:0.7}.e-button{display:inline-block;text-decoration:none;border:none;padding:20px;cursor:pointer;position:relative;overflow:hidden;font-family:Fahkwang;font-style:normal;font-weight:600;font-size:14px;line-height:18px;text-align:center;letter-spacing:1.27273px}.e-button.white{background:white;color:black}.e-button.black{background:black;color:white}

